.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  height: 7rem;
  margin-top: 20px;
}

.perspective-text {
  color: white;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  letter-spacing: -2px;
  text-transform: uppercase;
  height: 26rem;
  }
  
  .perspective-line {
    height: 50px;
    width: 15rem;
    font-weight: 700;
    margin: auto;
    padding: 0%;
    border: 10px;
    overflow: hidden;
    position: relative;
    justify-content: center;
    align-items: center;
    display: grid;
    flex-wrap: wrap;
  }
  
  .title_text {
    margin: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    line-height: 50px;
    transition: all 0.6s ease-in-out;
    width: 100%;
  }
  
  .perspective-line:nth-child(odd) {
    -webkit-transform: skew(60deg, -30deg) scaleY(0.667);
            transform: skew(60deg, -30deg) scaleY(0.667);
  }
  
  .perspective-line:nth-child(even) {
    -webkit-transform: skew(0deg, -30deg) scaleY(1.337);
            transform: skew(0deg, -30deg) scaleY(1.337);
  }
  
  .perspective-text:hover p {
    -webkit-transform: translate(0, 50px);
            transform: translate(0, 50px);
  }
  
  .perspective-line:nth-child(1) {
    left: 29px;
  }
  
  .perspective-line:nth-child(2) {
    left: 58px;
    
    background: #468DFF;
  }
  
  .perspective-line:nth-child(3) {
    left: 87px;
    background: green;
  }
  
  .perspective-line:nth-child(4) {
    left: 116px;
    background: #FFB25F;
  }
  
  .perspective-line:nth-child(5) {
    left: 145px;
  }
.TodoSearch {
  background: #F9FBFC;
  border-radius: 30px;
  border: 2px solid #202329;
  margin: 0 24px;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}
ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}
.TodoItem {
  background-color: #FAFAFA;
  border-radius: 30px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  width: 100px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}

form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 22px;
    margin-bottom: 26px;
    color: black;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
    resize: none;
  }
  
  textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button-add {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button-cancel {
    background: transparent;
  }
  
.CreateTodoButton {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}
.TodoHeader {
  /* background-color: #78839a; */
  padding: 25px 0;
  width: 100%;
  border-radius: 0 0 50px 50px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.TodoTitle {
  text-align: center;
  font-size: 40px;
}
.Footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  width: 100%;
  text-align: center;
  max-width: 75%;
  margin: 0 auto;
  /* background-color: #172a57; */

  border-radius: 25px 25px 0 0;
  box-shadow: rgba(0, 0, 0, 0.24) 0px -3px 8px;
}
a {
  text-decoration: none;
  color: #172a57;
  text-align: center;
  margin: 10px 20px;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}
.Footer-icon {
  font-size: 30px;
  margin-bottom: 5px;
  color: #172a57;
  margin: 5px 0;
  margin-right: 5px;
}

@media (max-width: 800px) {
  .Footer {
    max-width: 100%;
  }
}
@media (max-width: 500px) {
  a span {
    display: none;
  }
  .icon {
    font-size: 28px;
  }
}
.ModalBackground {
    background: rgba(32, 35, 41, .8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.Loading-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
  margin: 24px 0px 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400px;
}

.LoadingTodo-completeIcon,
.Loading-deleteIcon {
  border-radius: 50px;
  height: 40px;
  width: 48px;
}

.LoadingTodo-completeIcon {
  position: absolute;
  left: 12px;
}

.Loading-deleteIcon {
  position: absolute;
  top: -24px;
  right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
  background-size: 400% 400%;
  -webkit-animation: loadingAnimation 3s ease-in-out infinite;
          animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg,#1d334b 9%,#00d4ff);
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}
#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
} 





